import React, {useState, useEffect} from 'react';
import ArticlesList from '../components/ArticlesList';
import articles from './article-content';
import NotFoundPage from './NotFoundPage';
import articleContent from './article-content';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';


const ArticlePage = ({match}) => {
    //state lets us temporarily store information, eg informaiton we loaded to our server.
    //use react hooks
    //use the article's name to load the correct article
    const name = match.params.name;
    const article = articleContent.find(article => article.name===name);
    const [articleInfo, setArticleInfo] = useState({upvotes: 0, comments:[]});
    useEffect(()=>{
        const fetchData = async()=>{
            const result = await fetch(`/api/articles/${name}`);
            //result contains article info, status code for error handling
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    },[name]);
    //we are passing in an empty array, and useEffects will update this array everytime something changes.
    //by passing in an empty array, we're basically putting it as CONSTANT, aka calling useEffect only when the page first loads

    //defining articleINfo which we are going to populate when we send our request to the server
    //and setarticleinfo which will be the function that will update the articleinfo
    //then we are passing in the initial value or default value of articleinfo in usestate

    //useeffect lets us fetch data, to use with usestate
    if (!article){
        return <NotFoundPage />;
    }

    const otherArticles = articleContent.filter(article=>article.name!==name);

    return(
    <>
        <h1>{article.title}</h1>
       <UpvotesSection articleName ={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}/>
        {article.content.map((paragraph, key)=>
        (
            <p key={key}>{paragraph}</p>
        ))}
        <CommentsList comments = {articleInfo.comments}/>
        <AddCommentForm articleName = {name} setArticleInfo = {setArticleInfo} />
        <h3>Other Articles:</h3>
        <ArticlesList articles={otherArticles}/>
    </>
    );
}

export default ArticlePage;